.delivery {
  margin: 4rem 6rem;
}

.delivery h1 {
  font-size: 3rem;
}

.first-ser-reverse {
  padding: 80px 0px 80px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  margin-top: 4.5rem;
}
.first-ser {
  padding: 10px 0px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 4.5rem;
}

.del-text {
  width: 45%;
  text-align: start;
  font-size: 1.3rem;
}

.del-text h3 {
  padding-bottom: 1rem;
}

.image {
  position: relative;
  width: 50%;
  display: flex;
  justify-content: space-between;
  z-index: -99;
}

.image img {
  width: 49%;
  height: 350px;
  object-fit: cover;
  border-radius: 6px;
  box-shadow: -1px 1px 62px -18px rgba(0, 0, 0, 0.19);
}

.image img:nth-child(2) {
  position: absolute;
  top: -10%;
  right: 0;
}

@media screen and (max-width: 850px) {
  .delivery {
    margin: 4rem 3rem;
  }
  .first-ser {
    padding: 10px 0px 0px;
    display: flex;
    flex-direction: column-reverse;
    margin-top: 2.5rem;
  }
  .first-ser-reverse {
    padding: 0px 0px 10px;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
  }

  .del-text {
    width: 100%;
    font-size: 150%;
  }
  .del-text p {
    font-size: 65%;
  }
  .image {
    width: 100%;
    margin: 1.8rem 0;
  }
  .image img {
    height: 250px;
  }
}
